/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: #fef9e7;
  color: #4a4a4a;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  overflow-x: auto;
  overflow-y: auto;
}

/* Navigation Bar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #ffd966;
  padding: 12px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1000;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.navbar-logo {
  font-size: 1.8em;
  font-weight: bold;
  color: #4a4a4a;
  font-family: 'Pacifico', cursive;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links a {
  text-decoration: none;
  font-size: 1.1em;
  color: #4a4a4a;
  font-weight: bold;
  transition: color 0.3s ease, transform 0.2s ease;
}

.nav-links a:hover {
  color: #d4a200;
  transform: translateY(-2px);
}

.hamburger {
  display: none;
  cursor: pointer;
  font-size: 1.8em;
  color: #4a4a4a;
  transition: transform 0.3s ease;
}

.hamburger:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .nav-links {
      display: none;
  }

  .hamburger {
      display: block;
  }
}

/* Particles Background */
.particles {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.container {
  width: 90%;
  max-width: 900px;
  text-align: center;
  padding: 20px;
  background-color: #fff7d9;
  border-radius: 20px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  position: relative;
}

@media (max-width: 768px) {
  .container {
      max-width: 350px;
      padding: 15px;
  }
}

.header {
  font-size: 2em;
  font-family: 'Pacifico', cursive;
  color: #d4a200;
  margin-bottom: 20px;
}

.wish-text {
  font-size: 1.2em;
  margin: 15px 0 30px;
  color: #6b6b6b;
  line-height: 1.6;
}

.sneak-peek img {
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.sneak-peek img:hover {
  transform: scale(1.05);
}

.wish-button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #ffd966;
  color: #4a4a4a;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.wish-button:hover {
  background-color: #ffcb44;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  gap: 10px;
  margin-top: 25px;
}

.gallery img {
  width: 100%;
  max-width: 100px;
  border-radius: 10px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.gallery img:hover {
  transform: scale(1.1);
}

.gallery-button {
  background-color: #d4a200;
  color: rgb(0, 0, 0);
  padding: 12px 20px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

.gallery-button:hover {
  background-color: #ffcb44;
}

.plushy-sticker {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 60px;
  transform: rotate(-20deg);
}

.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  background: linear-gradient(135deg, #FFF5CC, #FFEB99, #FFE066);
  z-index: 999;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 6px solid rgba(255, 239, 184, 0.3);
  border-top: 6px solid #FFD966;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
